@import "../../global.scss";
.portfolio{
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;


    h1{
        font-size: 70px;
    }

    ul{
        font-weight: 500;
        margin: 20px;
        padding: 0;
        list-style: none;
        display: flex;

        
    }

    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        .item{
            width: 270px;
            height: 200px;
            border-radius: 18px;
            border: 1px solid ghostwhite;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all .5s ease;
            cursor: pointer;

            h3{
                position: absolute;
                font-size: 20px;
            }

            img{
                width: 100%;
                height: 100%;
                object-fit: contain;
                z-index: 1;
            }

            &:hover{
                background-color: $mainColor;
                img{
                    opacity: 0.3;
                    z-index: 0;
                }
            }
        }
    }
}